@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
:root {
  --bg: white;
  --primary: black;
}
body{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: linear-gradient(45deg, #fbda61, #ff5acd);
    padding-left: 10px;
}
.searcBar{
  margin-bottom: 70px;
}
.title {
  margin-top: 10px;
  margin-bottom: 30px;
  text-align: center;
}
.title h1{
  font-size: 30px;
}
.search {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.search input {
  max-width: 80%;
  padding: 3px 7px;
  font-size: 17px;
  border-radius: 5px;
  outline: none;
  border: 1px solid var(--primary);
}

.search button{
  font-size: 17px;
  padding: 3px;
  background: var(--primary);
  color: var(--bg);
  border-radius: 5px;
  outline: none;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.5s linear;
}

.search button:hover {
  background: var(--bg);
  border: 2px solid var(--primary);
  color: var(--primary);
}

.card{
    position: relative;
    height: 190px;
    width: 350px;
    background:#fff;
    border-radius: 20px;
    box-shadow: 0 35px 80px rgba(0,0,0,0.15);
    transition: 0.5s ease;
}
.card:hover{
    height: 450px;
    width: 400px;
}
.card .imgBox{
    position: absolute;
    width: 150px;
    height: 150px;
    background: #fff;
    left: 50%;
    top: -50px;
    transform: translateX(-50%);
    border-radius: 20px;
    box-shadow: 0 35px 80px rgba(0,0,0,0.35);
    overflow: hidden;
    transform: 0.5s ease;
}
.card:hover .imgBox{
    width: 200px;
    height: 200px;
}
.imgBox img{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.content{
    position:absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    overflow: hidden;

}
.content .detail{
    padding: 0px 40px 30px 40px;
    text-align: center;
    width: 100%;
    transition: 0.5 ease;
    transform: translateY(150px);
}
.card:hover .content .detail{
    transform: translateY(0);
}
.content .detail h2{
    font-size: 1.25em;
    font-weight: 600;
    color: #555;
    line-height: 1.2em;
}
.content .detail h2 span{
    font-size: 0.75em;
    font-weight: 500;
    color: #555;
    opacity: 0.5;
}
.data{
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
}
.data h3{
    font-size: 1.2em;
    color: #555;
    line-height: 1.2em;
    font-weight: 600;
}
.data h3 span{
    font-size: 0.85em;
    font-weight: 400;
    opacity: 0.5;
}
.actionBtn{
    display: flex;
    justify-content: space-between;
}
a{
    padding: 10px 30px;
    border: none;
    outline: none;
    text-decoration: none;
    border-radius: 7px;
    font-size: 1em;
    font-weight: 500;
    background: #ff5f95;
    color: #fff;
    cursor: pointer;
}
a:nth-child(2){
    color: #fff;
    background: #000;
    border: 1px solid rgb(61, 57, 57);
}